@font-face {
    font-family: 'panama';
    src: url('/fonts/panama-bold-webfont.woff2') format('woff2'),
         url('/fonts/panama-bold-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;

}

@font-face {
    font-family: 'nexarust';
    src: url('/fonts/NexaRustSans-Black.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}


html {
  font-size:10px;
}

body {
  font-size: 1.5em;
  color:$black;
}

h1, h2, h3, h4, h5, h6, {
  font-size: 5rem;
  font-family: 'Panama';
  font-weight: 600;
  letter-spacing:-1px;
}

a {
  text-decoration: none;
  color:$black;
}

p a {
  border-bottom: 1px dotted $gainsboro;
}

p {
  line-height: $base-line-height;
  font-size: modular-scale(0);
  color: $charcoal;
}

strong {
  color: $jet;
}

.nexa-rust {
  font-family: 'nexarust', sans-serif;
}

.title {
  word-wrap: break-word;
  // font-family: 'impact';
  // font-family: 'nexarust', sans-serif;  
  position: relative;
  z-index: 2;
  margin:0;
  font-size: 40px;
  z-index: 2;
  line-height:1.1;
  color: $vapor;
  // transform: skew(0, -3deg);
  &--long {
    font-size: 30px;
  }

  line-height:1.1;
  // inline link inside
  a {
    color: $vapor;
  }
  
  @include mod('albums') {
    text-align: center;
  }
  
  @include mod('pedals') {
    font-size: modular-scale(2);
    color: $base;
    text-align: center;
    margin-top: $base-spacing;
  }

  @include mod('tags') {

    font-size: modular-scale(3);
    margin-bottom: 0;
    color: $oil;
    text-align: center;
    
    @media all and (min-width : 768px) and (max-width : 1024px) {  
      font-size: modular-scale(4);
    }
    
    @media all and (min-width : 1025px) { 
      font-size: modular-scale(5);
    }          
  }
}

.title--single {
  max-width: 90%;
  margin-top: -9rem;
  font-size: 7rem;
  text-align: center;
  
  @media all and (min-width : 1025px) { 
    max-width: 600px;
    font-size: 9rem;    
  }
  
}

.title--page {
  font-family: 'nexarust';  
}


